<style>
	*,
	*::before,
	*::after {
		box-sizing: border-box;
	}

	html {
		font-size: 100%;
	}
	.text-blank{
		background: rgba(255,100,100,.5);
	}

	.sl_no-col{
		width:70px !important;
	}
	.species-list, .species-list input{
		font-size: .75rem;
	}
	.species-list td{
		vertical-align: middle;
	}
	.form-details{
		padding: 10px;
		background: rgba(130,170,0,.25);
	}
	.autocomplete{
		min-width: 200px !important;
	}
	.quality-cell{
		min-width: 300px !important;
	}
	.required-field-missing:after, .required label>div:after {
		content:" *";
		color: red;
	}
	.required-field-filled{
		color: green;
	}
	#forms-section{
		transition: all .5s;
		max-height: 63vh !important;
		overflow-y: scroll;
	}
	.required-field-missing{
		color: red;
	}
	.unmatched-name{
		border: 2px solid rgba(255,100,100,1);
		background: rgba(255,100,100,.5);
	}
	.matched-name{
		border: 2px solid rgba(100,200,100,1);
		background: rgba(100,255,100,.05);
	}
	.species-list .ui-autocomplete__input{
		min-width: 250px;
	}
	#info-button, #forms-button{
		font-size: 1.75rem;
		transition: color .5s;
	}
	#info-button:hover, #forms-button:hover{
		color: #ff9 !important;
		cursor: pointer;
	}
	.common_name-search ul{
		height: auto;
		max-height: 200px;
		overflow-y: scroll;
	}
	.forms-modal-table th, .forms-modal-table td{
		white-space: nowrap;
		text-align: center;
	}
	.pill-btn:hover{
		cursor: pointer;
	}
</style>
<template>
	<div class="container-fluida">
		<ui-modal ref="infoModal" size="large" :title="modal_title" :alignTop="true" @hide="modalClose">
			<ul v-if="modal_content=='instructions'">
				<li>Duration of the each count should be 30 minutes. As an example, if you are doing a 2 hour trail you will need to do 4 counts of 30 minutes each.</li>
				<li>Distance travelled is not important. However, for each 30-minute count, estimate the approx. distance covered and note it.</li>
				<li>All counts should be one way. Butterflies to be counted from Point A to Point B and not on return journey, if the same route is repeated. Additional species recorded on the return journey should be noted separately. If a different route is take on the return journey, it can be part of the count.</li>
				<li>Attempt to photograph all the species. If you are unable to identify to species level, mention the group/genus. For example, Grass Yellow/Eurema sp.</li>
				<li>Chose any time preferably sunny and when butterfly activity is more</li>
				<li>Please fill up and submit separate sheets for each 30 minuter counts/transects.</li>
				<li>In the information section, the questions with the red astrix are required and must be filled</li>
			</ul>
			<div class="user-forms" v-if="modal_content=='forms'">
				<table class="table table-sm forms-modal-table" v-if="selected_sheet==-1">
					<thead>
						<tr>
							<th>Sl. No.</th><th>Location</th><th>Date</th><th>No of Species</th><th>Upload Status</th><th>Actions</th>
						</tr>
					</thead>
					<tbody>
						<tr v-for="(form, fid) in all_forms" :class="form.submission=='submitted'?'table-success':'table-danger'">
							<td v-text="fid+1"></td>
							<td v-text="form.data.form.location"></td>
							<td v-text="form.data.form.date"></td>
							<td v-text="form.data.rows.length"></td>
							<td v-text="form.submission"></td>
							<td>

								<ui-button v-if="form.submission=='pending'" color="green" size="small" @click="submitForm(form.data)">Submit</ui-button>
								<ui-button color="primary" size="small" @click="selected_sheet=fid">View Sheet</ui-button>
							</td>
						</tr>
					</tbody>
				</table>
				<table class="table table-sm" v-else>
					<tbody>
						<tr v-for="col in form_cols" v-if="all_forms[selected_sheet].data.form[col[2]] != undefined">
							<td colspan="2" v-text="col[0]" class="border-end"></td>
							<td colspan="2" v-text="all_forms[selected_sheet].data.form[col[2]]" class="text-center"></td>
						</tr>
					</tbody>
					<tbody>
						<tr>
							<th class="sl_no-col">Sl. No</th>
							<th class="text-nowrap">Common Name</th>
							<th class="text-nowrap">No of Individuals</th>
							<th class="text-nowrap quality-cell">Remarks</th>
						</tr>
						<tr v-for="row in all_forms[selected_sheet].data.rows">
							<td v-for="cell in row" v-text="cell"></td>
						</tr>
					</tbody>
				</table>
				<div class="d-flex justify-content-center" v-if="selected_sheet!=-1">
					<ui-button color="red" size="normal" @click="selected_sheet=-1">Back</ui-button>
				</div>
			</div>
		</ui-modal>
		<div id="tabs-section">
			<ui-toolbar
				brand="BBM21:Butterfly Counts"
				:removeNavIcon="true"
				text-color="white"
				type="colored"
				:raised="true">
				<div slot="actions">
					<ui-icon-button color="primary" size="large" icon="info" tooltip="Directions/Instructions" tooltip-position="bottom" @click="showInfoModal()"></ui-icon-button>
					<ui-icon-button color="primary" size="large" icon="storage" tooltip="Saved Forms" tooltip-position="bottom" @click="showSavedForms()"></ui-icon-button>
				</div>
			</ui-toolbar>

			 <ui-tabs :fullwidth="true" :raised="true" type="icon-and-text">
				<ui-tab v-for="(tab, tab_no) in tabs" :key="tab_no" :selected="tab_no === 0">
					<div slot="header" class="my-custom-tab-header">
						<ui-icon slot="icon" :icon="tab[1]"></ui-icon><span :class="tabClass(tab_no)" v-text="tab[0]"></span>
					</div>

					<div id="forms-section">
						<div v-for="(fc, fc_no) in form_cols" v-if="tab_no < 2" class="mx-1">
							<ui-textbox
								floating-label
								:label="fc[0]"
								v-model="form_values[fc[2]]"
								v-if="showFormField(fc[2], tab_no)"
								:class="requiredFieldClass(fc[2])"
							></ui-textbox>
							<ui-autocomplete
								floating-label
								:label="fc[0]"
								v-model="form_values[fc[2]]"
								:suggestions="states"
								:class="requiredFieldClass(fc[2])"
								v-if="(tab_no==1) && (fc[2] == 'state')"
							></ui-autocomplete>
							<div class="row me-1" v-if="(tab_no==1) && (fc[2] == 'coordinates')">
								<ui-textbox
									floating-label
									:label="fc[0]"
									v-model="form_values.coordinates"
									class="col-9"
								></ui-textbox>
								<ui-button class="col" color="primary" size="small" @click="getCoordinates()">Get Coordinates</ui-button>
							</div>
							<ui-datepicker
								placeholder="Select a date"
								v-model="count_date"
								:class="requiredFieldClass(fc[2])"
								v-if="(fc[2] == 'date') && (tab_no == 1)"
							>Date</ui-datepicker>
						</div>
						<div class="species-list" v-if="tab_no == 2">
							<table class="table table-sm">
								<thead class="text-center">
									<tr>
										<th class="sl_no-col">Sl. No</th>
										<th class="text-nowrap">Common Name</th>
										<th class="text-nowrap">No of Individuals</th>
										<th class="text-nowrap quality-cell">Remarks</th>
										<th class="text-nowrap quality-cell">Data Quality Notes</th>
										<th class="text-nowrap"></th>
									</tr>
								</thead>
								<tbody>
									<tr v-for="(row, row_id) in row_values" :key="row_id">
										<td>
											<ui-textbox disabled v-model="row_values[row_id]['sl_no']"></ui-textbox>
										</td>
										<td>
											<ui-autocomplete
												v-model="row_values[row_id]['common_name']"
												:suggestions="common_names"
												:limit="50"
												:minChars="3"
												:autofocus="true"
												class='common_name-search'
												:class="inputSpeciesClass(row, 'common_name')"
											></ui-autocomplete>
										</td>
										<td>
											<ui-textbox
												v-model="row_values[row_id]['individuals']"
											></ui-textbox>
										</td>
										<td>
											<ui-textbox
												v-model="row_values[row_id]['remarks']"
											></ui-textbox>
										</td>
										<td>
											<ul v-if="row_comments[row.sl_no]" class="m-0 text-danger">
												<li v-for="c in row_comments[row.sl_no]" v-text="c"></li>
											</ul>
										</td>
										<td>
											<button class="btn btn-sm btn-danger" @click='removeRow(row.sl_no)'>Remove</button>
										</td>
									</tr>
								</tbody>
							</table>
							<div class="d-flex justify-content-center mb-5">
								<ui-button color="primary" icon="add" size="large" @click="addSpeciesRow()">Add another species</ui-button>
							</div>
						</div>
					</div>
				</ui-tab>
			</ui-tabs>
			<div class="d-flex justify-content-center">
				<ui-button color="green" :loading="submitting" size="large" :disabled="!form_validated" @click="submitData()">Submit Count Sheet</ui-button>
			</div>
		</div>
		<ui-alert @dismiss="show_alert = false" :type="alert_type" v-show="show_alert">
			{{alert_text}}
		</ui-alert>
	</div>
</template>

<script>
	import axios from 'axios';
	import synoptic from './synoptic.json'
	import boi from './boi.json'

	export default {
		name:"butterfly-count",
		data(){
			return {
				all_forms:[],
				form_values:{name:"",affiliation:"",phone:"",email:"",team_members:"",photo_link:"",location:"",state:"",coordinates:"",date:new Date(),start_time:"",end_time:"",altitude:"",distance:"",weather:"",comments:""},
				count_date:null,
				row_values:{},
				form_cols:[
					["Name of the Person taking the count", "text", "name"],
					["Affiliation (NGO/school, etc.)", "text" ,"affiliation"],
					["Contact Number", "text" ,"phone"],
					["E Mail ID", "text" ,"email"],
					["Name and/or number of team members", "text" ,"team_members"],
					["Link to photo records uploaded", "text" ,"photo_link"],
					["Location (Place name, nearest village/town)", "text" ,"location"],
					["State", "custom", "state"],
					["GPS Coordinates (from device)", "custom" ,"coordinates"],
					["Date", "date", "date"],
					["Start Time", "time" ,"start_time"],
					["End Time", "time" ,"end_time"],
					["Altitude, m", "text" ,"altitude"],
					["Total distance covered, km (approx. estimated)", "text" ,"distance"],
					["Weather (sunny, clouded, windy, etc.)", "text" ,"weather"],
					["Comments", "text" ,"comments"]
				],
				required_fields: ["name","email","location","date", "state"],
				row_cols:[
					["sl_no", "Sl No", "number"],
					["common_name", "Common Name", "text"],
					// ["scientific_name", "Binomial Name (if known)", "text"],
					["individuals", "No. of individuals", "number"],
					["remarks", "Remarks (Male/Female/seasonal form, etc.)", "text"]
				],
				states:["Andaman & Nicobar","Andhra Pradesh","Arunachal Pradesh","Assam","Bihar","Chandigarh","Chhattisgarh","Dadra & Nagar Haveli and Daman & Diu","Delhi","Goa","Gujarat","Haryana","Himachal Pradesh","Jammu & Kashmir","Jharkhand","Karnataka","Kerala","Ladakh","Lakshadweep","Madhya Pradesh","Maharashtra","Manipur","Meghalaya","Mizoram","Nagaland","Odisha","Puducherry","Punjab","Rajasthan","Sikkim","Tamil Nadu","Telangana","Tripura","Uttar Pradesh","Uttarakhand","West Bengal"],
				synoptic_names: synoptic,
				boi_names: boi,
				last_row_id:1,
				common_names:[],
				scientific_names:[],
				local_storage_fields:["name","affiliation","phone","email","team_members","state"],
				page_no:0,
				tabs:[
					["Respondent Details", "person"],
					["Date & Location Details", "explore"],
					["Species List", "list"]
				],
				modal_content:"instructions",
				modal_title:"Instructions for this Butterfly Count Sheet",
				submitting:false,
				show_alert:false,
				alert_text:"",
				alert_type:"",
				selected_sheet:-1,
			}
		},
		computed:{
			row_comments(){
			  	let op = {};
				Object.keys(this.row_values).forEach(row_id => {
					let row = this.row_values[row_id];
					op[row.sl_no] = [];

					if(row.individuals == ""){
						op[row.sl_no].push("No of individuals is blank please fill this field.")
					}

					if(this.inputSpeciesClass(row,"common_name") == "unmatched-name"){
						if(row.common_name == ""){
							op[row.sl_no].push("Common Name not entered.")
						} else {
							op[row.sl_no].push("Common Name doesn't match with our lists.")
						}
						 // Please check the name. If you are unsure, go ahead and validate it, we will review this in a short while.
					}

				})
				return op;
			},
			form_validated(){
				let required_set = true;
				let no_common_name = !this.checkCommonNameEntered();
				this.form_values.date = this.count_date;
				this.required_fields.forEach(f => {
					if(this.form_values[f] == ""){
						required_set = false;
					}
				})
				if(no_common_name){
					required_set = false;
				}

				return required_set;
			},
		},
		watch: {
			count_date: function (value, oldValue) {
				this.form_values.date = value;
			},
		},
		created(){
		},
		mounted(){
			this.init();
		},
		methods:{
			init(){
				this.initFormValues();
				this.populateAutocompleteLists();
				if(this.modal_content == "instructions")
					this.showInfoModal();
			},
			initFormValues(){
				this.form_values = {name:"",affiliation:"",phone:"",email:"",team_members:"",photo_link:"",location:"",state:"",coordinates:"",date:new Date(),start_time:"",end_time:"",altitude:"",distance:"",weather:"",comments:""},
				this.row_values = {};
				this.getStoredValues();
				this.addSpeciesRow();
				this.page_no = 0;
			},
			getStoredValues(){
				this.local_storage_fields.forEach(f => {
					if(window.localStorage.getItem(f) != null){
						this.form_values[f] = window.localStorage.getItem(f);
					}
				})
				if(window.localStorage.getItem("all_forms") != null){
					let un_submitted = false;
					this.all_forms = JSON.parse(window.localStorage.getItem("all_forms"));
					this.all_forms.forEach(af => {
						if(af.submission != "submitted"){
							un_submitted = true;
						}
					})
					if(un_submitted == true){
						this.showSavedForms();
					}
				}
			},
			showInfoModal(){
				this.modal_content = "instructions"
				this.modal_title = "Instructions for this Butterfly Count Sheet";
				this.openModal('infoModal')
			},
			showSavedForms(){
				this.modal_content = "forms"
				this.modal_title = "Your Butterfly Count Data Sheets";
				this.openModal('infoModal')
			},
			setStoredValues(){
				this.local_storage_fields.forEach(f => {
					if(this.form_values[f] != undefined){
						window.localStorage.setItem(f, this.form_values[f]);
					}
				})
				window.localStorage.setItem("all_forms", JSON.stringify(this.all_forms));
			},
			getCoordinates(){
				if (navigator.geolocation) {
					navigator.geolocation.getCurrentPosition(this.showPosition);
				} else {
					x.innerHTML = "Geolocation is not supported by this browser.";
				}
			},
			showPosition(position) {
				this.form_values.coordinates  = `${position.coords.latitude}°N, ${position.coords.longitude}°E`;
				// console.log(this.form_values.coordinates);
			},
			tabClass(p_no){
				let op = "required-field-filled"
				switch(p_no){
					case 0: if(this.form_values.name == "" || this.form_values.email =="")
								op = "required-field-missing"
							break;
					case 1: if(this.form_values.location == "" || this.form_values.state =="" || this.form_values.date =="")
								op = "required-field-missing"
							break;
					case 2: if(this.checkCommonNameEntered() == false)
								op = "required-field-missing"
							break;
				}
				return op;
			},
			checkCommonNameEntered(){
				let op = false;
				Object.keys(this.row_values).forEach(row_id => {
					if(this.row_values[row_id].common_name != ""){
						op = true;
					}
				})
				return op;
			},
			showFormField(fc, tab_no){
				let op = false;
				let col_id = -1
				this.form_cols.forEach((col, id) => {
					if(fc == col[2])
						col_id = id;
				})
				switch(tab_no){
					case 0: if(col_id < 6)
							op = true;
							break;
					case 1: if(col_id > 5 && col_id<16)
							if(fc !="state" && fc != "coordinates" && fc !="date"){
								op = true;
							}
							break;
					case 2: if(col_id > 20)
							op = true;
							break;
				}

				return op;
			},
			requiredFieldClass(fc){
				let op = "";
				if(this.required_fields.indexOf(fc) != -1){
					op = "required";
					if(this.form_values[fc] == ""){
						op += " required-empty";
					}
				}
				return op;
			},
			addSpeciesRow(){
				let op = {};
				this.row_cols.forEach(c => {
					if(c[0] == "sl_no"){
						op[c[0]] = this.last_row_id;
					} else if(c[0] == "individuals"){
						op[c[0]] = 1;
					}
					else {
						op[c[0]] = '';
					}
				})
				this.$set(this.row_values,this.last_row_id,op)
				this.last_row_id++;
			},
			populateAutocompleteLists(){
				let op = new Set();
				this.synoptic_names.forEach(sn => {
					op.add(sn[1])
				})
				this.boi_names.forEach(bn => {
					op.add(bn[1])
				})
				this.common_names = Array.from(op);
			},
			inputSpeciesClass(row, col){
				let op = "";

				switch(col){
					case "sl_no":
					case "individuals":
											if((row[col] == "") || (row[col] == null)){
												op = "text-blank"
											}
											break;
					case "common_name": op = "unmatched-name";
										if(this.common_names.indexOf(row[col]) != -1 && row[col]!= ""){
											op = "matched-name"
										}
										break;
				}
				return op;
			},
			inputDetailsClass(col){
				let op = "";
				if((this.form_values[col] == "") || (this.form_values[col] == null))
					if(col == 'state'){
						op = "auto-text-blank";
					} else {
						op = "text-blank";
					}
				return op;
			},
			titleCase(str) {
				let splitStr = str.toLowerCase().split('_');
				for (let i = 0; i < splitStr.length; i++) {
					splitStr[i] = splitStr[i].charAt(0).toUpperCase() + splitStr[i].substring(1);
				}
				return splitStr.join(' ');
			},
			removeRow(row_id){
				let count = 1;
				let op = {};

				delete this.row_values[row_id];

				Object.values(this.row_values).forEach(v => {
					let val = v;
					val["sl_no"] = count;
					op[count] = val;
					count++
				})

				this.row_values = op;
				this.last_row_id--;
			},
			submitData(){
				let post_data = {
					form:this.form_values,
					rows:this.row_values
				}
				this.submitForm(post_data);
			},
			submitForm(d){
				this.submiting = true;
				let exists_flag = false;
				this.all_forms.forEach(f => {
					if(f.data == d){
						exists_flag = true;
					}
				})
				if(exists_flag == false){
					this.all_forms.push({data: d, submission:"pending"});
				} else {
					console.log("it found it!!");
				}
				this.setStoredValues();

				const axios = require('axios');
				let that = this;

				axios.post('/butterfly_count/pwa_post', d)
					.then(function (response) {
						if(response.status == 200){
							console.log(response)
							that.all_forms[that.all_forms.length-1].submission = "submitted";
							that.alert_text = "Success!! Butterfly Count Form Submitted Successfully. ";
							that.alert_type = "success";
							that.show_alert = true;
							that.setStoredValues();
						}
					})
					.catch(function (error) {
						that.alert_text = "Error!! Butterfly Count Form could not be Submitted. This could be becase of a network. You can try again later. In case this issue persists, please drop an email to rohitmg@gmail.com describing the issue you are having ";
						that.alert_type = "warning";
						that.show_alert = true;
					})
					.then(function (response){
						that.initFormValues();
						that.submiting = false;
					});
			},
			openModal(ref) {
				this.$refs[ref].open();
			},
			modalClose(){
				this.selected_sheet = -1
				this.modal_content = "instructions"
				this.modal_title = "Instructions for this Butterfly Count Sheet"
			},
		}
	};
</script>
